import BasePage from 'components/BasePage';
import React, { useContext } from 'react';
import AppContext from '../App/context';
import {
  Container,
  Title,
  SubTitle,
  BounceButton,
} from '../components/BaseStyles';

const Cover: React.FC = () => {
  const { goToNext } = useContext(AppContext);
  return (
    <BasePage cover>
      <Container>
        <Title>AI </Title>
        <SubTitle>AI    </SubTitle>
        <BounceButton size="xxlarge" padding="large" onClick={goToNext}>
          
        </BounceButton>
      </Container>
    </BasePage>
  );
};

export default Cover;
