import React, { useContext } from 'react';
import AppContext from 'App/context';
import styled from 'styled-components';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
  PointText,
} from 'components/BaseStyles';
import { CustomBtn, CustomBtnDiv } from 'components/EtcStyles';
import AirFresh from 'assets/images/airfresh.gif';

const Div = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
`;

const Page: React.FC = () => {
  const { goToNext } = useContext(AppContext);
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>  AI </ContentTitle>
          <ExplainBox>
            <div>(AI)    . </div>
            <div>
               <PointText>  AI  </PointText> ,
              AI !
            </div>
          </ExplainBox>
          <Content>
            <Div>
              <img src={AirFresh} alt="" />
            </Div>
          </Content>
        </DivBase>
        <CustomBtnDiv>
          <CustomBtn
            onClick={() => {
              goToNext();
            }}
          >
            
          </CustomBtn>
        </CustomBtnDiv>
      </Container>
    </BasePage>
  );
};

export default Page;
