import React from 'react';
import styled from 'styled-components';
import Lottie from 'react-lottie';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
} from 'components/BaseStyles';
import ButtonGroup from 'components/ButtonGroup';
import GuestList from 'assets/lotties/58305-guestlist.json';
import Code from 'assets/lotties/61918-code-animation.json';
import HelpBox from 'components/HelpBox';

const Point = styled.span`
  color: #1696e5;
`;

const WhatIsSupervisedLearning: React.FC = () => {
  const guestListOptions = {
    loop: true,
    autoplay: true,
    animationData: GuestList,
    rendererSettings: {
      preserveAspectRatio: 'xMidYMid slice',
    },
  };
  const codeOptions = {
    loop: true,
    autoplay: true,
    animationData: Code,
    rendererSettings: {
      preserveAspectRatio: 'xMidYMid slice',
    },
  };

  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle> ?</ContentTitle>
          <ExplainBox style={{ display: 'block' }}>
            <div>
               (Input data)   (Output
              data)   .
            </div>
            <div>
               <Point>     </Point>
               <Point></Point> !
            </div>
          </ExplainBox>
          <Content style={{ display: 'flex', marginTop: '4rem' }}>
            <div style={{ width: '60%', height: '100%' }}>
              <Lottie options={guestListOptions} />
            </div>
            <div
              style={{
                width: '30%',
                height: '100%',
                display: 'flex',
                justifyContent: 'center',
                marginTop: '3rem',
                marginLeft: '-10rem',
              }}
            >
              <Lottie options={codeOptions} width={500} height={350} />
            </div>
          </Content>
          <HelpBox title="   ?">
            <div>AI    ,   !</div>
            <div>    , </div>
            <div>      .</div>
          </HelpBox>
        </DivBase>
        <ButtonGroup />
      </Container>
    </BasePage>
  );
};

export default WhatIsSupervisedLearning;
